import {useContext} from 'react'
import Card from './shared/Card'
import {FaTimes, FaEdit} from 'react-icons/fa'
import FeedbackContext from '../Context/FeedbackContext'

function FeedbackItem({ip, rating, text, ibool}) {
    
    const {handleDelete,handleEdit} = useContext(FeedbackContext);
  
    return (
        <Card ibool={ibool}>
           <div className='card'>
            <div className="num-display">{rating}</div>
            <button className="close-icon" onClick={()=> handleDelete(ip)} ><FaTimes color="purple" /></button>
             <button className="edit-icon close-icon" onClick={()=> handleEdit({ip,text,rating})}><FaEdit color="purple" /></button>
            <div className="text-display">{text}</div>
           </div>
        </Card>
    )
}

export default FeedbackItem
